import Vue from "vue";
import Router from "vue-router";
// 设备 控制主页
import Mydevice from "./views/Mydevice.vue";
// 设备 控制主页
import Home from "./views/Home.vue";
// 设备 设置页面
import Setting from "./views/Setting.vue";

// 设置 的 子页面


Vue.use(Router);

export default new Router({
    base: process.env.BASE_URL,
    routes: [
        // 我的设备列表
        {
            path: '/',
            component: Mydevice
        },
        // 设备 控制主页
        {
            path: '/home',
            name: "home",
            component: Home
        },
        // 设备 设置页面
        {
            path: '/setting',
            // name: "setting",
            component: Setting,
            children: [{
                    path: 'timingswitch',
                    // name: 'timingswitch',
                    component: () => import("./components/TimingSwitch.vue"),
                },
                {
                    path: 'airfilter',
                    // name: 'resetairfilter',
                    component: () => import("./components/ResetAirFilter.vue"),
                },
                {
                    path: 'eqpshare',
                    // name: 'eqpshare',
                    component: () => import("./components/EqpShare.vue"),
                },
            ],
        },
        // 仿 设备操作页面
        {
            path: '/device',
            component: () => import("./views/device.vue"),
        }
    ]
});